export const common = {
  // 날짜 포맷 함수
  getElapsedFormat(seconds) {
    if(seconds == 0) return ''
    if(seconds >= 60 ) {
      let minute = Math.floor(seconds / 60)
      let second = seconds - minute*60
      return `(${minute}min ${second}sec)`
    } else {
      return `(${seconds}sec)`
    }
  }
}
