<template>
<v-container>
  <v-row 
    v-for="step in steps"
    :key="step.id">
    <v-col>
      <v-card :style="step.exitCode =='COMPLETED' || step.exitCode =='REMOVED' ? 'border: 1px solid white;' : 'border: 1px solid red;'">
        <v-list-item three-line>
          <v-list-item-content>
            <v-list-item-title class="headline">{{ step.name }}</v-list-item-title>
            <v-list-item-subtitle>Execution ID : {{ step.executionId }}</v-list-item-subtitle>
            <v-list-item-subtitle>Step ID : {{ step.id }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
        <v-card-text>         
          <v-simple-table dense>    
            <template v-slot:default>
              <thead>
                <tr bgcolor="lightgrey">
                  <th class="text-center">Exit Code</th>
                  <th class="text-center">Start Time</th>
                  <th class="text-center">End Time</th>
                  <th class="text-center">Commit Count</th>
                  <th class="text-center">Read Count</th>
                  <th class="text-center">Process Count</th>
                  <th class="text-center">Write Count</th>          
                  <th class="text-center">Rollback Count</th>
                </tr>
              </thead>
              <tbody>
                <tr>  
                  <td class="text-center" :style="step.exitCode == 'COMPLETED' || step.exitCode == 'REMOVED' ? '' : 'color: red'">{{ step.exitCode }}</td> 
                  <td class="text-center">{{ step.stime }}</td> 
                  <td class="text-center">{{ step.etime }}</td>        
                  <td class="text-center">{{ step.commitCount }}</td> 
                  <td class="text-center">{{ step.readCount }}</td> 
                  <td class="text-center">{{ step.filterCount }}</td> 
                  <td class="text-center">{{ step.writeCount }}</td>           
                  <td class="text-center">{{ step.rollbackCount}}</td>    
                </tr>
              </tbody>
            </template>
          </v-simple-table>
          <v-expansion-panels>
            <v-expansion-panel>
              <v-expansion-panel-header v-if="step.exitMessage !=''">
                <div>{{ step.exitCode }} Message</div>
              </v-expansion-panel-header>
              <v-expansion-panel-content>{{ step.exitMessage}}</v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
  <div align=right v-show="execution.exitCode=='UNKNOWN'">
    <v-dialog
      v-model="dialog"
      persistent
      max-width="510"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn 
          small
          outlined
          color="red"
          v-bind="attrs"
          v-on="on" 
        >Removed
        </v-btn>
      </template>
      <v-card>
        <v-container fluid>
        <v-textarea
          sole
          v-model="exitMessage" 
          label="Change the job to REMOVED state. Record what you did."
        ></v-textarea>
        </v-container>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="dialog = false;setRemovedJob(execution)"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>   
  </div>
</v-container> 
</template>
<script>
import axios from "axios"
import { common } from "../func/common"

export default {
  data () {
    return {
      steps: [],
      execution: this.$route.params,
      reveal: false,
      exitMessage: '',
      dialog: false 
    }
  },
  created() {
    this.findByInstanceId(this.execution.id)
  },
  filters: {
    elpasedmask: function(value) {
      return common.getElapsedFormat(value)
    }
  },
  methods: {
    findByInstanceId(executionId) {
      axios.get(`${this.$batchAPIUri}/job/executionId/${executionId}`)
        .then(res => { 
          this.steps = res.data
        })
        .catch(err => {
          console.error(err)
        })
    },
    setRemovedJob(execution) {
      if(this.exitMessage == '') {
        alert('You need to record the work you have done before you can save it.')
        return
      }
      axios.put(`${this.$batchAPIUri}/job/setRemovedJob?executionId=${execution.id}&exitMessage=${this.exitMessage}`)
        .then(res => { 
          console.log(res)
          // 현재 페이지 reload 하면 parameter는 사라진다. 그러므로 store를 사용해야 한다.
          // this.$router.push({ name: 'step', params:job })
          this.$router.push({ name: 'job' })
        })
        .catch(err => {
          console.error(err)
        })
    }
  }
}
</script>